.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: start;
      align-items: flex-start;
}

.modal {
  position: absolute;
  top: 10%;
  bottom: 10%;
  left: 10%;
  right: 10%;
  border: 1px solid #cccccc;
  background: #ffffff;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  border-radius: 5px;
  outline: none;
  padding: 50px;
}

input, textarea {
  font-family: "Architects Daughter", cursive;
  padding: 5px;
}

label, input, textarea {
  display: block;
  margin: 5px 0;
  width: 100%;
}

label {
  font-weight: bold;
}

textarea {
  height: 100px;
}



